// ÊãºÊé•ËØ∑Ê±ÇÂú∞ÂùÄ
function getWebsocketUrl() {
  // Ëß£ÊûêhttpUrl‰ª•Âä®ÊÄÅËé∑Âèñdomain‰ø°ÊÅØ
  switch (httpUrl.pathname) {
    case '/v1.1/chat':
      modelDomain = 'general'
      break
    case '/v2.1/chat':
      modelDomain = 'generalv2'
      break
    case '/v3.1/chat':
      modelDomain = 'generalv3'
      break
    case '/v3.5/chat':
      modelDomain = 'generalv3.5'
      break
  }

  return new Promise((resolve) => {
    // ÊãºÊé•ÂêÑÁßç‰ø°ÊÅØ‰ª•ÂÆåÊàêÊé•Âè£Èâ¥ÊùÉ
    var apiKey = API_KEY
    var apiSecret = API_SECRET

    var url = 'wss://' + httpUrl.host + httpUrl.pathname

    var host = location.host
    var date = new Date().toGMTString()
    var algorithm = 'hmac-sha256'
    var headers = 'host date request-line'
    var signatureOrigin = `host: ${host}\ndate: ${date}\nGET ${httpUrl.pathname} HTTP/1.1`
    var signatureSha = CryptoJS.HmacSHA256(signatureOrigin, apiSecret)
    var signature = CryptoJS.enc.Base64.stringify(signatureSha)
    var authorizationOrigin = `api_key="${apiKey}", algorithm="${algorithm}", headers="${headers}", signature="${signature}"`
    var authorization = btoa(authorizationOrigin)

    // ÊãºÊé•‰∏∫url
    url = `${url}?authorization=${authorization}&date=${date}&host=${host}`
    resolve(url)
  })
}

class TTSRecorder {
  constructor({ appId = APPID } = {}) {
    this.appId = appId
    this.status = 'init'
  }

  // ‰øÆÊîπÁä∂ÊÄÅ
  setStatus(status) {
    this.onWillStatusChange && this.onWillStatusChange(this.status, status)
    this.status = status
  }

  start() {
    total_res = '' // ËØ∑Á©∫ÂõûÁ≠îÂéÜÂè≤
    return this.connectWebSocket().finally(() => {
      console.log('startÂáΩÊï∞ÊâßË°åÂÆåÊØï')
    })
  }

  // ËøûÊé•websocket
  connectWebSocket() {
    return new Promise((resolve) => {
      this.setStatus('ttsing')
      return getWebsocketUrl().then((url) => {
        let ttsWS
        if ('WebSocket' in window) {
          ttsWS = new WebSocket(url)
        } else if ('MozWebSocket' in window) {
          // ttsWS = new MozWebSocket(url)
        } else {
          alert('ÊµèËßàÂô®‰∏çÊîØÊåÅWebSocket')
          return
        }

        this.ttsWS = ttsWS
        ttsWS.onopen = () => {
          this.webSocketSend()
        }

        ttsWS.onmessage = (e) => {
          this.result(e.data).then(() => {
            resolve()
          })
        }

        ttsWS.onerror = () => {
          clearTimeout(this.playTimeout)
          this.setStatus('error')
          alert('WebSocketÊä•ÈîôÔºåËØ∑f12Êü•ÁúãËØ¶ÊÉÖ')
          console.error(`ËØ¶ÊÉÖÊü•ÁúãÔºö${encodeURI(url.replace('wss:', 'https:'))}`)
        }

        ttsWS.onclose = (e) => {
          console.log(e)
        }
      })
    })
  }

  // websocketÂèëÈÄÅÊï∞ÊçÆ
  webSocketSend() {
    console.log(modelDomain)
    var params = {
      header: {
        app_id: this.appId,
        uid: 'fd3f47e4-d',
      },

      parameter: {
        chat: {
          domain: modelDomain,
          temperature: 0.5,
          max_tokens: 1024,
        },
      },

      payload: {
        message: {
          text: [
            {
              role: 'user',
              content: question_text,
            },
          ],
        },
      },
    }
    console.log(JSON.stringify(params))
    this.ttsWS.send(JSON.stringify(params))
  }

  // websocketÊé•Êî∂Êï∞ÊçÆÁöÑÂ§ÑÁêÜ
  result(resultData) {
    return new Promise((resolve) => {
      let jsonData = JSON.parse(resultData)
      total_res += jsonData.payload.choices.text[0].content
      if (jsonData.payload.choices.status === 2) {
        console.log('ÂõûÁ≠îÔºö' + total_res)
        resolve(total_res)
      }

      // ÊèêÈóÆÂ§±Ë¥•
      if (jsonData.header.code !== 0) {
        alert(`ÊèêÈóÆÂ§±Ë¥•: ${jsonData.header.code}:${jsonData.header.message}`)
        console.error(`${jsonData.header.code}:${jsonData.header.message}`)
        return
      }

      if (jsonData.header.code === 0 && jsonData.header.status === 2) {
        this.ttsWS.close()
        bigModel.setStatus('init')
      }
    })
  }
}

